import { createTool } from '@mastra/core/tools';
import { z } from 'zod';
import { getBraveMCPClient } from '../mcp/brave-mcp-client';

// MCP„ÉÑ„Éº„É´„ÇíÂèñÂæó„Åó„Å¶„Ç≠„É£„ÉÉ„Ç∑„É•
interface MCPTool {
  execute: (params: {
    context: Record<string, unknown>;
    mastra?: unknown;
    runtimeContext?: unknown;
  }) => Promise<unknown>;
}

let mcpTools: Record<string, MCPTool> | null = null;

async function getMCPTools() {
  if (mcpTools) {
    return mcpTools;
  }
  
  console.log('üîß MCP„ÉÑ„Éº„É´„ÇíÂàùÂõûÂèñÂæó‰∏≠...');
  const mcpClient = getBraveMCPClient();
  mcpTools = await mcpClient.getTools();
  console.log('üì¶ ÂèñÂæó„Åó„ÅüMCP„ÉÑ„Éº„É´:', Object.keys(mcpTools));
  
  return mcpTools;
}

// Brave MCP„ÉÑ„Éº„É´„ÇíMastra„ÉÑ„Éº„É´„Å®„Åó„Å¶„É©„ÉÉ„Éó
export const braveMCPSearchTool = createTool({
  id: 'brave-mcp-search',
  description: 'Brave MCP„Çí‰ΩøÁî®„Åó„Å¶WebÊ§úÁ¥¢„ÇíÂÆüË°å„Åó„Åæ„Åô',
  inputSchema: z.object({
    query: z.string(),
    count: z.number().optional().default(10),
  }),
  outputSchema: z.object({
    searchResults: z.string(),
    success: z.boolean(),
  }),
  execute: async ({ context, mastra, runtimeContext }) => {
    const { query, count } = context;
    
    try {
      console.log(`üîç Brave MCP„ÉÑ„Éº„É´„ÅßWebÊ§úÁ¥¢„ÇíÂÆüË°å: "${query}"`);
      
      // MCP„ÉÑ„Éº„É´„ÇíÂèñÂæó
      const tools = await getMCPTools();
      
      // braveSearch_brave_web_search „ÉÑ„Éº„É´„ÇíÊé¢„ÅôÔºàMCPClient.getTools()„ÅØ„Çµ„Éº„Éê„ÉºÂêç„Åß„Éó„É¨„Éï„Ç£„ÉÉ„ÇØ„Çπ„Çí‰ªò„Åë„ÇãÔºâ
      const braveSearchToolName = Object.keys(tools).find(name => 
        name.includes('brave_web_search')
      );
      
      if (!braveSearchToolName) {
        console.error('‚ùå Brave WebÊ§úÁ¥¢„ÉÑ„Éº„É´„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì');
        console.error('Âà©Áî®ÂèØËÉΩ„Å™„ÉÑ„Éº„É´:', Object.keys(tools));
        return {
          searchResults: JSON.stringify({ error: 'Brave WebÊ§úÁ¥¢„ÉÑ„Éº„É´„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì' }),
          success: false,
        };
      }
      
      console.log(`üîß ‰ΩøÁî®„Åô„Çã„ÉÑ„Éº„É´: ${braveSearchToolName}`);
      const braveSearchTool = tools[braveSearchToolName];
      
      // Mastra„ÉÑ„Éº„É´„Å®„Åó„Å¶ÂÆüË°å
      console.log('üìù „ÉÑ„Éº„É´ÂÆüË°å„Éë„É©„É°„Éº„Çø:', {
        query,
        count,
      });
      
      const searchResult = await braveSearchTool.execute({
        context: {
          query,
          count,
        },
        mastra,
        runtimeContext,
      });
      
      console.log('‚úÖ Brave MCPÊ§úÁ¥¢ÂÆå‰∫Ü');
      console.log('üìä Ê§úÁ¥¢ÁµêÊûú:', searchResult);
      console.log('üìä Ê§úÁ¥¢ÁµêÊûú„ÅÆ„Çø„Ç§„Éó:', typeof searchResult);
      console.log('üìä Ê§úÁ¥¢ÁµêÊûú„ÅÆ„Ç≠„Éº:', searchResult ? Object.keys(searchResult) : 'null');
      
      // Ê§úÁ¥¢ÁµêÊûú„ÅÆÂÜÖÂÆπ„ÇíÁ¢∫Ë™ç
      let resultString = '';
      if (searchResult && typeof searchResult === 'object') {
        // MCP„ÉÑ„Éº„É´„ÅÆÁµêÊûú„ÅØcontentÈÖçÂàó„ÇíÊåÅ„Å§ÂèØËÉΩÊÄß„Åå„ÅÇ„Çã
        if ('content' in searchResult && Array.isArray(searchResult.content)) {
          console.log('üìä content ÈÖçÂàó:', searchResult.content);
          // contentÈÖçÂàó„ÅÆÊúÄÂàù„ÅÆË¶ÅÁ¥†„ÇíÂèñÂæó
          const firstContent = searchResult.content[0];
          if (firstContent && typeof firstContent === 'object' && 'text' in firstContent) {
            console.log('üìä text „Éó„É≠„Éë„ÉÜ„Ç£:', firstContent.text);
            resultString = firstContent.text;
          } else {
            resultString = JSON.stringify(searchResult.content);
          }
        }
        // result„Éó„É≠„Éë„ÉÜ„Ç£„Åå„ÅÇ„ÇãÂ†¥Âêà
        else if ('result' in searchResult) {
          console.log('üìä result „Éó„É≠„Éë„ÉÜ„Ç£:', searchResult.result);
          resultString = typeof searchResult.result === 'string' 
            ? searchResult.result 
            : JSON.stringify(searchResult.result);
        } 
        // data„Éó„É≠„Éë„ÉÜ„Ç£„Åå„ÅÇ„ÇãÂ†¥Âêà
        else if ('data' in searchResult) {
          console.log('üìä data „Éó„É≠„Éë„ÉÜ„Ç£:', searchResult.data);
          resultString = typeof searchResult.data === 'string' 
            ? searchResult.data 
            : JSON.stringify(searchResult.data);
        }
        // „Åù„ÅÆ‰ªñ„ÅÆÂ†¥Âêà
        else {
          console.log('üìä „Åù„ÅÆ‰ªñ„ÅÆÂΩ¢Âºè:', searchResult);
          resultString = JSON.stringify(searchResult);
        }
      } else if (typeof searchResult === 'string') {
        resultString = searchResult;
      } else {
        resultString = JSON.stringify(searchResult);
      }
      
      console.log('üìä ÊúÄÁµÇÁöÑ„Å™ÁµêÊûúÊñáÂ≠óÂàó:', resultString.substring(0, 200) + '...');
      
      return {
        searchResults: resultString,
        success: true,
      };
    } catch (error) {
      console.error('‚ùå Brave MCPÊ§úÁ¥¢„Ç®„É©„Éº:', error);
      console.error('„Ç®„É©„Éº„ÅÆË©≥Á¥∞:', {
        name: error instanceof Error ? error.name : 'Unknown',
        message: error instanceof Error ? error.message : String(error),
        stack: error instanceof Error ? error.stack : undefined,
      });
      return {
        searchResults: JSON.stringify({ error: error instanceof Error ? error.message : 'Unknown error' }),
        success: false,
      };
    }
  },
}); 